<template>
  <div id="home">
    <!-- <p>Home组件</p> -->
    <router-view></router-view>
    <ul class="menu">
      <li :class="{'active':co}">
        <router-link to="/home/list">笔记列表</router-link>
      </li>
      <li :class="{'active':cc}">
        <router-link :to="{name:'homeuser'}">个人中心</router-link>
      </li>
    </ul>
  </div>
</template>
<script>
import Menu from '@/components/menu.js'
export default {
  data () {
    return {
      co: Boolean,
      cc: Boolean
    }
  },
  created () {
    var _this = this
    Menu.$on('mark', function (num) { // 经事件总线Menu，接收由兄弟组件传入的mark变量的参数值num，通过回调函数作处理
      if (num === 1) {
        _this.co = true
        _this.cc = false
      } else if (num === 2) {
        _this.cc = true
        _this.co = false
      }
    })
  }
}
</script>
<style scoped>
#home {
  width: 100%;
  /*border: 1px solid red;*/
}
.menu {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  font-size: 0; /* 使该元素子代的inline-block元素 水平方向无间隙 */
}
.menu li, .menu li a {
  display: inline-block;
}
.menu li {
  height: 40px;
  width: 50%;
  text-align: center;
  background-color: #b0d7d6;
}
.active {
  background: #343a3d !important;
}
.menu li:nth-child(1) {
  box-sizing: border-box;
  border-right: 1px solid;
}
.menu li a {
  width: 100%;
  font: bold 22px/40px 楷体,微软雅黑，arial;
  text-decoration: none;
}
</style>



