import Vue from 'vue'
import Vuex from 'vuex'
// 在数组中查找 所有出现的x，并返回一个包含匹配索引的数组
function findall (a, x) {
  var results = []
  var len = a.length
  var pos = 0
  while (pos < len) {
    pos = a.indexOf(x, pos)
    if (pos === -1) { // 未找到就退出循环完成搜索
      break
    }
    results.push(pos) // 找到就存储索引
    pos += 1 // 并从下个位置开始搜索
  }
  return results
}
// 引用类型深拷贝
function deepCopy (s) {
  const d = []
  for (let k in s) {
    if (typeof s[k] === 'object') {
      d[k] = deepCopy(s[k])
    } else {
      d[k] = s[k]
    }
  }
  return d
}
// ES6数组去重
function unique (arr) {
  return Array.from(new Set(arr))
}

Vue.use(Vuex)
const store = new Vuex.Store({
  state: {
    count: 0,
    click_item_num: 0, // 暂存当前点击条目的下标
    status: false, // 记录当前的登录状态
    note_username: '',
    note_password: '',
    question: '',
    question_answer: '',
    list: []
  },
  mutations: {
    upp () {
      // this.state.count++
      store.state.count++
    },
    clearAll (state) { // 先清除store，再清除localStorage！（否则即使先清除storage，store中的数据也会因页面刷新或定时保存，再次被存入storage）
      state.count = 0
      state.click_item_num = 0
      state.status = false // 恢复到退出登录状态
      state.note_username = ''
      state.note_password = ''
      state.question = ''
      state.question_answer = ''
      state.list = []
      localStorage.removeItem('store')
      console.log(store.state)
    },
    register1 (state, value) {
      store.state.note_username = value.regname
      store.state.note_password = value.regpass
      store.state.question = value.question
      store.state.question_answer = value.question_answer
    },
    changeStatus (state) {
      state.status = !state.status
    },
    addItem (state, value) {
      state.list.unshift(value)
    },
    updateItem (state, value) {
      state.list[state.click_item_num].note_title = value.note_title
      state.list[state.click_item_num].note_content = value.note_content
    },
    sortList (state) {
      /* 条目动态以时间降序重排(list条目太多时代价过高，意义不大) */
      var timeList = []
      for (let i = 0; i < state.list.length; i++) {
        (function (ind) {
          timeList.push({'cur_time': state.list[ind].note_time.curDate, 'item_index': ind})
        }(i))
      }

      var curTime = [] // 准备存放 重排序前 各个条目的简化时间，类似["201912210129", "201912210132", "201912202032", "201912202030"]
      for (let i = 0; i < timeList.length; i++) {
        curTime.push(timeList[i].cur_time)
      }
      var _curTime = deepCopy(curTime) // 准备对curTime数组重排序，原数组_curTime元素顺序 不受影响

      // console.log(curTime) // 简化时间数组集合
      curTime = unique(curTime) // 简化时间数组中 元素去重。["201912210129", "201912210132", "201912202030"]

      for (let i = 0; i < curTime.length - 1; i++) { // 已去重的简化时间数组 降序。["201912210132", "201912210130", "201912210129"]
        for (let j = 0; j < curTime.length - 1 - i; j++) {
          if (curTime[j] < curTime[j + 1]) {
            var ls = curTime[j]
            curTime[j] = curTime[j + 1]
            curTime[j + 1] = ls
          }
        }
      }
      // console.log(curTime) // 去重，降序 简化时间数组集合

      // 依次遍历数组curTime（降序且元素唯一）中的目标元素，查找在_curTime原数组（可能有重复元素）中包含该目标元素的 所有索引
      var itemIndex = ''
      for (let i = 0; i < curTime.length; i++) {
        (function (ind) {
          itemIndex += findall(_curTime, curTime[ind]) // 对每次遍历不同的目标元素 返回的索引数组 直接进行字符串拼接。这里是'1,230'
        }(i))
      }

      itemIndex = itemIndex.replace(',', '').split('') // 先去除字符串中的 逗号，再分割字符串为数组
      // console.log(itemIndex) // 重排序后的数组元素 在旧数组中 对应的 索引集合。这里是[1,2,3,0]

      // list条目数组以 发布时间降序 重排序
      var listNew = []
      for (let i = 0; i < itemIndex.length; i++) {
        listNew.push(state.list[itemIndex[i]])
      }
      state.list = listNew
    }
  },
  actions: {
  }
})
export default store



