<template>
  <div id="home-list">
    <!-- <p>HomeList组件</p> -->
    <p v-if="pageList.length === 0">空空如也。。。</p>
    <ul v-else class="list">
      <v-touch
        id="v-touch"
        v-for="(item, index) of pageList"
        :key="index"
        tag="li"
        @tap="clickItem(index)"
        @panright="rightMove(index)"
        @panleft="leftMove(index)"
        :class="['list-item',{'left-move':(index === current),'current-item':(index === ob)}]"
      >
      <!--
      先渲染出列表所有条目表达式中===左边的的index值，之后根据对应条目事件的发生再次初始化current或ob
      -->
      <!--
      <li @tap="clickItem(1)" @panright="rightMove(1)" :classs="{'current-item':(1 === ob)}"></li>
      <li @tap="clickItem(2)" @panright="rightMove(2)" :classs="{'current-item':(2 === ob)}"></li>
      -->
      <!--
      当ob为2时，只有第二项的current-item class生效
      -->
        <div class="list-item-left">
          <p class="title">{{item.note_title}}</p>
            <span class="note-time">{{getDayName(item)}}</span>
            <!-- <span class="note-time">{{getDate === item.note_time.day ? '今天 ' + item.note_time.time : item.note_time.currentDate}}</span> -->
            <span class="more-content" @click="updateItem(index)">&raquo;</span>
        </div>
        <div class="list-item-right">
          <span
            @click="deleteItem(index)"
          >删除</span>
        </div>
      </v-touch>
    </ul>
    <p class="add-list-item">
      <svg @click="addItem" xmlns="http://www.w3.org/2000/svg" class="si-glyph-pencil" viewBox="0 0 17 16">
        <title>992</title>
        <defs></defs>
        <g stroke="none" stroke-width="1" fill="none" fill-rule="evenodd">
          <path d="M16.479,2.536 L14.474,0.546 C13.935,0.00899999997 13.091,-0.019 12.59,0.482 L3.47,9.616 C3.47,9.616 1.158,14.936 1.001,15.366 C0.876,15.706 1.307,16.137 1.605,15.991 C2.085,15.754 7.345,13.471 7.345,13.471 L16.487,4.299 C16.989,3.797 17.018,3.073 16.479,2.536 L16.479,2.536 Z M2.312,13.906 L4.033,10.279 L4.015,10.985 L5.013,10.971 L14.537,1.529 L15.796,2.765 L6.612,11.73 L6.385,12.482 L6.871,12.968 L3.06,14.624 L2.312,13.906 L2.312,13.906 Z" fill="#434343" class="si-glyph-fill"></path>
        </g>
      </svg>
    </p>
  </div>
</template>

<script type="text/javascript">
import store from '@/store/store'
import Menu from '@/components/menu.js'
export default {
  name: 'HomeList组件',
  data () {
    return {
      ob: Number,
      current: -1,
      last: null,
      oo: false,
      leftMark: false
    }
  },
  computed: {
    pageList () {
      return store.state.list
    },
    getDate () {
      var date = new Date()
      var year = date.getFullYear().toString()
      var month = date.getMonth() + 1
      var strDate = date.getDate()
      return year + month + strDate
    }
  },
  created () {
    /* 将当前页面mark标记为1，通过事件总线Menu，准备与兄弟组件Home进行通信 */
    Menu.$emit('mark', 1)
  },
  methods: {
    clickItem (value) {
      this.current = -1 // 点击聚焦前，先重置左右滑动事件
      this.ob = value
    },
    addItem () {
      this.$router.push('/add')
    },
    updateItem (value) {
      store.state.click_item_num = value // 将点击条目编号存入store，为update组件针对性提取信息做准备
      this.$router.push('/update')
    },
    deleteItem (value) {
      var askDelete = window.confirm('删除该条目？')
      if (askDelete) {
        store.state.list.splice(value, 1)
        alert('已删除该条目')
        this.current = -1
      }
    },
    leftMove (value) {
      this.ob = value // 执行下方操作前先聚焦
      if (this.last === value) { // 若连续两次均左滑同一对象
        this.oo = !this.oo // 通过取反，2次中必有1次重置current（1左滑-2左滑归位-3左滑）！
        if (this.oo) { // 由于是panleft,panright事件，该条件下不需要任何操作
          // this.current = -1 // 重置current为-1
          return false
        }
        this.current = value
        this.last = value // 暂存此次左滑对象的编号于last
        this.leftMark = true // 一旦对象左滑，边标记其状态 leftMark 为true，为随后
      } else { // 若连续两次左滑不同对象，会自动重置之前的操作对象！
        this.current = value
        this.last = value
        this.leftMark = true
      }
    },
    rightMove (value) {
      this.ob = value // 执行下方操作前先聚焦
      this.current = -1 // 重置current为-1
      // if (this.last === value && this.leftMark === true) { // 若连续两次均左滑同一对象,且上一次是左滑操作
      //   this.current = -1 // 重置current为-1
      // }
    },
    getDayName (value) {
      var td = new Date()
      td = new Date(td.getFullYear(), td.getMonth(), td.getDate())
      var od = new Date(value.note_time.currentDate) // '2019-12-21 15:00:02'时间格式不兼容IE和Safari
      od = new Date(od.getFullYear(), od.getMonth(), od.getDate())
      var xc = (od - td) / 1000 / 60 / 60 / 24
      if (xc < -1) {
        return '前天 ' + value.note_time.time
      } else if (xc < 0) {
        return '昨天 ' + value.note_time.time
      } else if (xc === 0) {
        return '今天 ' + value.note_time.time
      } else {
        return value.note_time.currentDate
      }
      // else if (xc < -2) {
      //   return -xc + '天前'
      // } else if (xc < 2) {
      //   return '明天'
      // } else if (xc < 3) {
      //   return '后天'
      // } else {
      //   return xc + '天后'
      // }
    }
  }
}
</script>

<style type="text/css" scoped>
#home-list {
  /*border: 1px solid green;*/
  margin: 0 2%;
  overflow: hidden;
}
.list {
  text-align: center;
}
.list-item {
  position: relative;
  display: inline-block;
  width: 94%;
  padding: 10px 8px;
  background: #e1caca;
  margin-bottom: 6px;
  border-radius: 10px;
  text-align: left;
  font: 16px/32px 'arial';
  transition: transform .2s linear;
}
.left-move {
  transform: translateX(-16%);
}
.more-content, .note-time {
  font: 14px/20px 楷体,arial;
  color: blue;
}
.more-content {
  float: right;
  display: inline-block;
  padding: 14px 0 14px 14px;
  transform: translateY(-50%);
  line-height: 2px;
}
.current-item {
  background: #4eb2df;
}
.current-item .list-item-left {
  color: #fff;
}
.list-item-left {
  padding: 0 2%;
}
.list-item-left .title {
  overflow: hidden;
  text-overflow:ellipsis;
  white-space: nowrap;
}
.current-item .more-content, .current-item .note-time {
  color: #fff;
}
.list-item-right {
  position: absolute;
  top: 0;
  right: -16%;
  height: 100%;
  width: 12%;
  text-align: center;
  line-height: 84px;
  font-weight: 600;
  color: #a58888;
}
.add-list-item {
  display: inline-block;
  position: fixed;
  left: 50%;
  bottom: 10%;
  width: 30%;
  transform: translateX(-50%);
  border-radius: 5px;
}
svg {
  width: 45px;
  height: 45px;
  margin: 10px;
}
</style>



