<template>
  <div id="ap">
    <h1>Cecile's Note😏</h1>
    <!-- <p>App组件</p> -->
    <router-view></router-view>
  </div>
</template>
<script>
export default {
  name: 'app组件'
}
</script>
<style>
#ap {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  /*border: 1px solid red;*/
  overflow: hidden;

  /* 解决上下拉动滚动条时卡顿、慢 */
  -webkit-overflow-scrolling: touch;
  overflow-scrolling: touch;
}
/* 组件边框 */
/*#register-login {
  border: 1px solid green;
}
#add {
  border: 1px solid blue;
}
#update {
  border: 1px solid black;
}
#home-user {
  border: 1px solid brown;
}*/

.go-back {
  position: relative;
  height: 50px;
}
.back-btn {
  position: absolute;
  top: 0;
  left: 6%;
  font: bold 24px/24px 楷体,arial;
  color: red;
}
#v-touch {
  touch-action: pan-y !important; /* 解决v-touch中使用左右滑动影响上下滑动bug */
}
h1 {
  position: relative;
  font: bold 28px/42px arial;
  padding: 4% 0 6%;
}
input {
  border-radius: 0; /* 只留border-bottom，ios会默认有个border-radius弧度角，需除去 */
}
</style>



