<template>
  <div id='add'>
    <!-- <p>Add组件</p> -->
    <p class="go-back">
      <span class="back-btn" @click="$router.back()">👈</span>
    </p>
    <div>
      <div>
        <input id="note-title" type="text" name="" placeholder="请输入标题"
          v-model="note_title"
        >
      </div>
      <div>
        <textarea id="note-content" name="" placeholder="请输入内容"
          v-model="note_content"
        ></textarea>
      </div>
      <p class="add-list-item">
        <button @click="submitNote">提交</button>
      </p>
    </div>
  </div>
</template>

<script type="text/javascript">
import store from '@/store/store'
function getNowFormatDate () {
  var date = new Date()
  var month = date.getMonth() + 1
  var strDate = date.getDate()
  var hours = date.getHours()
  var minutes = date.getMinutes()
  if (month >= 1 && month <= 9) {
    month = '0' + month
  }
  if (strDate >= 0 && strDate <= 9) {
    strDate = '0' + strDate
  }
  if (hours <= 9) {
    hours = '0' + hours
  }
  if (minutes <= 9) {
    minutes = '0' + minutes
  }
  var day = date.getFullYear().toString() + month + strDate
  var time = hours + ':' + minutes
  var currentDate = date.getFullYear().toString() + '/' + month + '/' + strDate + ' ' + hours + ':' + minutes
  var curDate = date.getFullYear().toString() + month + strDate + hours + minutes
  return {'currentDate': currentDate, 'curDate': curDate, 'day': day, 'time': time}
}
export default {
  name: 'add组件',
  data () {
    return {
      note_title: '',
      note_content: ''
    }
  },
  methods: {
    submitNote () {
      if (this.note_title === '' || this.note_content === '') {
        alert('请补全内容')
      } else {
        store.commit('addItem', {
          note_title: this.note_title,
          note_content: this.note_content,
          note_time: getNowFormatDate()
        })
        this.$router.back(-1)
      }
    }
  }
}
</script>

<style type="text/css" scoped>
#note-title, #note-content {
  font: 16px/24px 楷体;
  width: 90%;
  border: 1px solid blue;
}
#note-title:focus, #note-content:focus {
  outline: none;
}
#note-title {
  border-width: 0 0 1px;
}
#note-content {
  height: 300px;
  border-width: 0 0;
}
.add-list-item {
  position: fixed;
  left: 50%;
  bottom: 10%;
  width: 60%;
  transform: translateX(-50%);
}
.add-list-item button {
  display: inline-block;
  width: 100%;
  height: 100%;
  background: rgba(172, 178, 107, .5);
  border-radius: 5px;
  border-width: 0;
  font: 16px/32px 楷体;
  text-decoration: none;
}
</style>



