<template>
  <div id="register-login">
    <!-- <p>register-login组件</p> -->
    <div id="register" v-if="isReg">
      <div class="account-login">
      <input id="username-reg" type="text" name="" placeholder="用户名" maxlength="16" v-model="reg_username">
      <input id="password-reg" type="text" name="" placeholder="密码" maxlength="16" v-model="reg_password">
      <input id="question" type="text" name="" placeholder="密保问题" v-model="question">
      <input id="question-answer" type="text" name="" placeholder="问题答案" v-model="question_answer">
      <p class="tips">提示：(用户名、密码) 须同时包含字母和数字，无特殊符号，且至少6位</p>
      <p class="add-list-item">
        <button @click="register">注册</button>
        <!-- <button @click="cancel">取消</button> -->
      </p>
    </div>
    </div>
    <div id="login" v-else>
      <div class="account-login">
        <input id="username" type="text" name="" placeholder="用户名" maxlength="16" v-model="username">
        <input id="password" type="text" name="" placeholder="密码" maxlength="16" v-model="password">
        <p class="tips">提示：(用户名、密码) 须同时包含字母和数字，无特殊符号，且至少6位</p>
        <p class="add-list-item">
          <button  @click="login">登录</button>
          <button  @click="findKey">找回密码</button>
        </p>
      </div>
    </div>
  </div>
</template>

<script type="text/javascript">
import store from '@/store/store'
function CheckPassWord (_str) { // 必须为字母加数字且长度不小于8位
  var str = _str
  if (str == null || str.length < 6) {
    alert('(用户名、密码)不能为空，且至少6位！')
    return false
  }
  var reg1 = new RegExp(/^[0-9A-Za-z]+$/)
  if (!reg1.test(str)) {
    alert('(用户名、密码)不能 含有特殊符号！')
    return false
  }
  var reg2 = new RegExp(/[A-Za-z].*[0-9]|[0-9].*[A-Za-z]/)
  if (!reg2.test(str)) {
    alert('(用户名、密码)需要 同时包含字母和数字！')
    return false
  } else {
    return true
  }
}
export default {
  name: 'login组件',
  data () {
    return {
      isReg: null,
      reg_username: null,
      reg_password: null,
      question: '',
      question_answer: '',
      username: null,
      password: null
    }
  },
  created () { // 根据是否有注册记录，渲染注册、或登录页面
    if (store.state.note_username === '' || store.state.note_password === '') {
      this.isReg = true
    } else {
      this.isReg = false
    }
    return this.isReg
  },
  methods: {
    /* cancel () {
      this.isReg = false
    }, */
    register () {
      if (CheckPassWord(this.reg_username) && CheckPassWord(this.reg_password)) {
        if (this.question === '' || this.question_answer === '') {
          alert('请补充 密保问题，问题答案！')
          return false
        }
        store.commit('register1', {
          regname: this.reg_username,
          regpass: this.reg_password,
          question: this.question,
          question_answer: this.question_answer
        })
        this.isReg = false
      }
    },
    login () {
      if (CheckPassWord(this.username) && CheckPassWord(this.password)) {
        if (store.state.note_username === this.username && store.state.note_password === this.password) {
          store.commit('changeStatus')
          this.$router.push('/home/list')
        } else {
          alert('用户名或密码有误')
        }
      }
    },
    findKey () {
      var find = prompt('请回答您的密保问题：' + store.state.question + '？', '')
      if (find === null || find === '') { // 未输入,或输入为''
        return false
      } else if (find === store.state.question_answer) {
        alert('验证成功！\r\n用户名: ' + store.state.note_username + '\r\n密码: ' + store.state.note_password)
        this.username = store.state.note_username
        this.password = store.state.note_password
      } else { // 密保问题回答错误
        alert('密保问题 回答错误！')
      }
    }
  }
}
</script>

<style type="text/css" scoped>
.tips {
  font: bold 12px/18px 楷体,arial;
  color: red;
}
.account-login {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  margin: auto;
  height: 190px;
  width: 60%;
}
#username,
#password,
#username-reg,
#password-reg,
#question,
#question-answer {
  font: 16px/24px 楷体;
  width: 80%;
  border-color: blue;
  border-style: solid;
  border-width: 0 0 1px;
}
#username,
#username-reg,
#password-reg,
#question,
#question-answer {
  margin-bottom: 4%;
}
#username:focus,
#password:focus,
#username-reg:focus,
#password-reg:focus,
#question:focus,
#question-answer:focus {
  outline: none;
}
.add-list-item {
  position: fixed;
  left: 50%;
  width: 60%;
  transform: translateX(-50%);
}
.add-list-item button {
  display: inline-block;
  width: 100%;
  height: 100%;
  background: rgba(172, 178, 107, .5);
  border-radius: 5px;
  border-width: 0;
  font: 16px/32px 楷体;
  text-decoration: none;
}
.add-list-item button:nth-child(1) {
  margin-bottom: 6%;
}
</style>



